body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Gill Sans Std', 'Lato' !important;
  font-weight: 300; }

p {
  margin: 0; }

.grid-wrapper {
  height: 100vh;
  width: 85%;
  display: grid;
  grid-template-columns: 3fr 9fr 5fr 1fr 1.5fr;
  grid-template-rows: 4fr 2fr 3fr 3fr 4fr 3fr 2fr 2fr;
  margin: auto; }

nav {
  grid-row: 1 / span 1;
  grid-column: 2 / span 2;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.left-right-margin {
  margin: 0px 15px; }

.align-items-center {
  align-items: center; }

.overlay-menu-link {
  text-transform: uppercase;
  margin: 5px 0px;
  font-size: 80%;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }

.overlay-menu-link:hover {
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }

.social-icon-base {
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }

.social-icon-base:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  cursor: pointer;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }

.social-icon-twit {
  background-image: url(/static/media/twitter.6e1e3491.svg); }

.social-icon-face {
  background-image: url(/static/media/facebook.1888960a.svg);
  width: 35px;
  height: 35px; }

.social-icon-inst {
  background-image: url(/static/media/instagram.100f7e9a.svg); }

.social-icon-gith {
  background-image: url(/static/media/github.fcd20a99.svg); }

.social-icon-vime {
  background-image: url(/static/media/vimeo.3afef8bb.svg); }

.hea {
  width: auto;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(/static/media/event-arrow.878da6ad.svg); }

.sidebar {
  grid-row: 3 / span 4;
  grid-column: 1 / span 1;
  display: flex;
  flex-direction: column;
  height: 65%;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  display: flex;
  justify-content: space-between; }

.sidebar-social-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%; }

.overlay-menu-title-div {
  display: flex;
  justify-content: center;
  align-items: center; }

.overlay-menu-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 85%;
  height: 85vh;
  margin: auto;
  background-color: #fff; }

.col-space-between {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.footer-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 70%;
  background-color: #fff;
  margin: auto;
  min-height: 500px; }

.text-align-right {
  text-align: right; }

.full-height {
  height: 100%; }

.medium-height {
  min-height: 130px; }

.featured-text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 70%;
  background-color: #fff;
  margin: auto;
  position: absolute;
  bottom: 40px;
  right: 0;
  padding: 30px;
  box-shadow: 0px 3px 50px rgba(0, 0, 0, 0.28); }

.col-general-page-wrapper {
  margin: 100px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }

.row-center {
  display: flex;
  justify-content: center;
  align-items: center; }

.row-space-between {
  display: flex;
  justify-content: space-between; }

.row-flex-start {
  display: flex;
  justify-content: flex-start; }

.row-align-right {
  display: flex;
  justify-content: flex-end; }

.row-general-page-wrapper {
  margin: 100px 0px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.small-bottom-margin {
  margin-bottom: 10px; }

.medium-bottom-margin {
  margin-bottom: 20px; }

.medium-top-margin {
  margin-top: 20px; }

.small-left-margin {
  margin-left: 10px; }

.text-bold {
  font-weight: 500; }

.primary-color {
  color: #ff4303; }

.header-company-title {
  color: #ff4303;
  font-size: 120%;
  text-transform: uppercase;
  grid-column: 3 / span 1;
  grid-row: 2 / span 1;
  display: flex;
  justify-content: flex-end;
  font-weight: 600; }

.header-company-baseline {
  grid-row: 7 / span 1;
  grid-column: 2 / span 1;
  display: flex;
  align-items: flex-end;
  font-size: 120%;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 0;
  font-weight: 600; }

.overlay-menu-company-title {
  font-size: 300%;
  font-weight: 400;
  margin-bottom: -20px; }

.overlay-menu-section-title {
  color: #ff4303;
  font-weight: 700;
  font-size: 90%; }

.footer-section-title {
  font-weight: 600;
  text-transform: uppercase; }

.title-margin {
  margin-top: 40px; }

.card-maintitle {
  font-weight: 700;
  font-size: 130%;
  text-transform: uppercase; }

.standard-text-paragraph {
  line-height: 23px;
  font-size: 15px;
  font-weight: 100;
  text-align: left; }

.styled-square-header {
  background: #FF4202;
  grid-row: 4 / span 4;
  grid-column: 3 / span 4;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  z-index: -1;
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s; }

.overlay-menu-title-decoration {
  height: 3px;
  width: 15px;
  border-radius: 30px;
  background-color: #FF4202;
  margin-left: 15px;
  display: block; }

.footer-bottom-decoration {
  height: 10px;
  width: 65%;
  background-color: #FF4202; }

.featured-square {
  height: 65%;
  width: 70%;
  background-color: #FF4202;
  position: absolute;
  bottom: 0;
  left: 0; }

.styled-circle-header-left {
  background-color: #FF4202;
  border-radius: 70px;
  width: 70px;
  height: 70px; }

.header-main-image {
  background-image: url(/static/media/header-1.6e4e5e3b.jpeg);
  background-size: cover;
  grid-row: 3 / span 4;
  grid-column: 2 / span 2;
  -webkit-animation-duration: 2s;
          animation-duration: 2s; }

.featured-bg-image {
  background-image: url(/static/media/bootcamp.321b56c7.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 70%;
  height: 80%;
  position: absolute;
  top: 0;
  right: 85px; }

.header-left-bottom {
  grid-column: 1 / span 1;
  grid-row: 6 / span 1;
  display: flex;
  align-items: flex-end; }


.event-block-container {
  grid-row: 6 / span 3;
  grid-column: 3 / span 2;
  box-shadow: 0px 3px 50px rgba(0, 0, 0, 0.28);
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 7%;
  z-index: 0; }

.featured-wrapper {
  position: relative;
  height: 400px;
  width: 370px;
  margin: 50px; }

.project-info-detailpage-wrapper {
  position: relative;
  height: 350px;
  width: 370px;
  margin: 50px; }

.primary-lable {
  padding: 3px 8px;
  max-width: 50%;
  background: #FF4202;
  display: flex;
  justify-content: center;
  color: #fff;
  margin-left: auto;
  text-align: center;
  font-weight: 400; }





